
.argremment-bottom {
  text-align: center;
}
.warp {
width: 100%;
height: auto;
position: relative;
box-sizing: border-box;
overflow: auto;
z-index:2;
}
.pfx {
  position: fixed;
  bottom:0;
  left: 0;
  z-index: auto;
  width: 100%;
  height: 4.5rem;
}
.pfx-top {
  position: fixed;
  bottom:0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 4.5rem;
}




